import React from 'react';
import { Alert } from 'reactstrap';

const Example = () => {
  return (
    <Alert color="success">
      <i className="fas fa-check md-18"></i> <strong>Success!</strong> A copy of your selected purchase orders has been
      sent to your customers.
    </Alert>
  );
};

export default Example;



